.Party{
  position: fixed;
  left:     2.5rem;
  bottom:   8rem;
}

.Party ul{
  list-style-type: none;
  padding: 0;
  margin:  0;

  max-width: 250px;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: 1fr 1fr;

  grid-column-gap: 15px;
}

.Party li{
  color:       #FFFFF0;
  font-weight: bold;
  text-shadow: 1px 1px 1px #000;
  text-align:  center;
  grid-row:    6 / 1;

  /* transition-timing-function: ease-in; */
  transition: 0.25s ease all;
  -webkit-transform:  translateY(200%);
          transform:  translateY(200%);
}

.Party li.in-party{
  transition-timing-function: ease-out;
  transition: 0.25s;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.Party li img{
  height: 80px; 
  image-rendering: -moz-crisp-edges; 
  image-rendering: pixelated;
}

.Party li img,
.Party li p{
  display: block;
  margin:  auto;
}

#partyList.walking li img{
  -webkit-animation: walk 0.3s;
          animation: walk 0.3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes walk {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg);  }
  30%  { -webkit-transform: rotate(5deg); transform: rotate(5deg);  }
  50%  { -webkit-transform: rotate(0deg); transform: rotate(0deg);  }
  70%  { -webkit-transform: rotate(-5deg); transform: rotate(-5deg); }
  100% { -webkit-transform: rotate(0deg); transform: rotate(0deg);  }
}

@keyframes walk {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg);  }
  30%  { -webkit-transform: rotate(5deg); transform: rotate(5deg);  }
  50%  { -webkit-transform: rotate(0deg); transform: rotate(0deg);  }
  70%  { -webkit-transform: rotate(-5deg); transform: rotate(-5deg); }
  100% { -webkit-transform: rotate(0deg); transform: rotate(0deg);  }
}

.Timeline{
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;

  display:   flex;
  flex-flow: row;
  z-index: 2;
}

.Timeline ol {
	position:   relative;
	display:    flex;
	margin:     100px;
	height:     4px;
	background: #333;
}
.Timeline ol::before,
.Timeline ol::after {
	content: "";
	position: absolute;
	top: -8px;
	display: block;
	width:  0;
	height: 0;
  border-radius: 10px;
	border: 10px solid #333;
}
.Timeline ol::before {
	left: -5px;
}
.Timeline ol::after {
	right:        -10px;
	border:        10px solid transparent;
	border-right:  0;
	border-left:   20px solid #000;
  border-radius: 3px;
}

/* ---- Timeline elements ---- */
ol li {
	position: relative;
	display:  inline-block;
	float:    left;
	width:    500px;
  height:   50px;
}

ol li .point {
	content: "";
	top: -4px;

  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);

	display: block;
	width: 6px;
	height: 6px;
	border: 4px solid #333;
	border-radius: 50%;
	background: #fff;
  position: absolute;
  transition: 0.3s ease all;
}

#eventDescription {
  transition: 0.3s ease height;

  position: fixed;
  top:      100px;
  right:    2rem;
  z-index:  1;
  width:    600px;

  padding:    5px 10px;
  box-shadow: 0 0 5px #979AA4;

  background: url(/justin-and-dragons/static/media/stat-block-top-texture.f7a151f2.png),
              url(/justin-and-dragons/static/media/paper-texture.1663ba32.png);
  background-size:     100% auto;
  background-position: top center;
  background-repeat:   no-repeat,repeat;

  pointer-events: none; /* Allow scrolling while mouse is over description */

  /* text attributes */
  color:       #000;
  text-align:  left;
  font-family: Verdana, sans-serif;
  font-size:   20px;
  font-weight: normal;
  line-height: 1.6;
}

#eventDescription .date,
#eventDescription .event{
  font-size:   24px;
  font-family: 'Marck Script', cursive;
}

#eventDescription .date{
  padding-bottom: 15px;
  border-bottom:  1px solid rgb(130,32,0);
}

ol li .pointDate{
  font-family: 'Marck Script', cursive;
  text-shadow: 1px 1px 1px #000;
  color: #FFFFF0;
  font-size: 1.6rem;
  text-align: center;
  transition: 0.3s ease all;
}

/* ---- Show description when event is active ---- */
ol li.active .pointDate{
  color: rgb(130,32,0);
}
ol li.active .point {
  width:      15px;
  height:     15px;
  border:     4px solid rgb(130,32,0);
  background: rgb(130,32,0);
  top:       -9px;
}

